body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: 'Quicksand', sans-serif;
  margin: 0;
  padding: 0;
}

.logo-rivero{
  border-radius: 50%;
  height: 4em;
}

.card{
  display: inline-flex;
  width: 18rem;
}

.foto-portafolio{
  height: auto;
  width: 20%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}
.cropcircle{
    align-content: center;
    background: #eee no-repeat center;
    background-size: cover;
    border-radius: 100%;
    height: 250px;
    width: 250px;
}

#image.cropcircle{
    background-image: url(/static/media/rivero-portafolio.e2baaaf0.jpeg);
}

.info, .pic{
  margin-top: 10%;
}

@media only screen and (max-width: 768px) {
  .pic{
    display: flex;
    justify-content: center;
  }
}

@media only screen and (max-width: 768px) {
  .pic{
    display: flex;
    justify-content: center;
  }
}


@media only screen and (max-width: 992px) {
  .pic{
    margin-top: 15%;
  }
}

.tracking{
  -webkit-animation: tracking-in-contract 0.8s cubic-bezier(0.950, 0.050, 0.795, 0.035) both;
  animation: tracking-in-contract 0.8s cubic-bezier(0.950, 0.050, 0.795, 0.035) both;
  text-align: center;	        
}

